import React, { Component } from 'react';
import './App.css';
import logo from './img/logo.png';
import discord from './img/discord.png';
import telegram from './img/telegram.png';
import rus from './img/rus.png';
import eng from './img/eng.png';
//import TronWeb from 'tronweb';
import { InputGroup, 
  InputGroupAddon, 
  Input, 
  Button, 
  InputGroupText, 
  Modal, 
  ModalHeader, 
  ModalBody,
  ModalFooter, 
  UncontrolledTooltip, 
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  Card, 
  CardImg, 
  CardText, 
  CardBody,
  CardTitle,
  Spinner,
 } from 'reactstrap';
import configuration from './config';
import configuration_ru from './config_ru';

const SUN_IN_TRX = 1000000;
const ENERGY_LIMIT = 10000000;
const ZERO_ADDRESS = "410000000000000000000000000000000000000000"
const NUMBER_OF_UNITS = 6;
const PERIOD = 3600;
//const PERIOD = 6; //Shasta
const CONTRACT_ADDRESS = "TJYNMUtUeDTEQGHKQQ7ZijMkh1E2kGKg4C";
const TWGOLD_CONTRACT_ADDRESS = "TGDTCTDmBDa8uPWbF3vnLLpjLwqPLkaWdt";
//const CONTRACT_ADDRESS = "TRgtTkYYnT6VS2jPdmxvDTJzW4wtQtptyP"; //Shasta
const ADMIN = "TGMJ1TMLCKpU71rN6CTwiQBPaErBq9W9D8";
//const ADMIN = "TDoN4gE9uPPw8eJ8PfBQUFcdFV6DPRpggQ"; //Shasta
const LOADING = -1;

let tronWeb = false;
let sponsor = ZERO_ADDRESS;
let refLink = window.location.search.slice(3);
let lang = 'en';

if (refLink == 'en') {
  document.cookie = `lang=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;`;
} else if (refLink == 'ru') {
  lang = 'ru';
  document.cookie = `lang=ru; max-age=31536000; path=/;`;
}
else {
  let langCookie = document.cookie.split(';').filter((item) => item.trim().startsWith('lang=ru'));
  if (langCookie.length > 0) {
    lang = 'ru';
  }
}

let units = configuration.units;
let faq = configuration.faq;
let startInfo = configuration.startInfo;
let referralInfo = configuration.referralInfo;
let footerLinks = configuration.footerLinks;
let sponsorWarning = configuration.sponsorWarning;
let twGoldInfo = configuration.twGoldInfo;
let messages = configuration.messages;

if (lang == 'ru') {
  units = configuration_ru.units;
  faq = configuration_ru.faq;
  startInfo = configuration_ru.startInfo;
  referralInfo = configuration_ru.referralInfo;
  footerLinks = configuration_ru.footerLinks;
  sponsorWarning = configuration_ru.sponsorWarning;
  twGoldInfo = configuration_ru.twGoldInfo;
  messages = configuration_ru.messages;
}


if(refLink.length > 30 && refLink.length < 36) {
  sponsor = escape(refLink);
  document.cookie = `ref=${sponsor}; max-age=31536000; path=/;`;
}
else {
  let refCookie = document.cookie.split(';').filter((item) => item.trim().startsWith('ref='));
  if (refCookie.length > 0) {
    sponsor = refCookie[0].slice(4);
  }
}


class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      refModal: false,
      faqModal: false,
      sponsorValid : false,
      sponsorInalid : false,
      showSponsorWarning: false,
      playerRefList : [],
      navbarOpen : false,
      address : messages.welcome,
      gameInfo : { balance : 0, payouts : 0, players : 0, units : 0, doomsday : 0 },
      playerInfo : {withdrawable : 0, repurchase : 0, refEarnings : 0, 
        noFirstUnit : true, time : LOADING, units : new Array(NUMBER_OF_UNITS).fill(0),
        },
    };
    this.getInfo = this.getInfo.bind(this);
    this.withdraw = this.withdraw.bind(this);
    this.deposit = this.deposit.bind(this);
    this.buy = this.buy.bind(this);
    this.tokens = this.tokens.bind(this);
    this.checkSponsor = this.checkSponsor.bind(this);
    this.toggleRefModal = this.toggleRefModal.bind(this);
    this.toggleFAQModal = this.toggleFAQModal.bind(this);
    this.navbarToggle = this.navbarToggle.bind(this);
    this.sponsor = ADMIN;
    this.units = units;
  }

  async getGameInfo() {
    let game =  await this.contract.getGameInfo().call();
    this.setState({gameInfo: { balance : (game[0].toNumber() / SUN_IN_TRX), 
        payouts : (game[3].toNumber() / SUN_IN_TRX), players : game[1].toNumber(),
        units : game[2].toNumber(), doomsday : game[4].toNumber() }});
  }

  async getPlayerInfo() {
    let player =  await this.contract.getPlayerInfo(this.account.address).call();
    let  playerUnits = new Array(NUMBER_OF_UNITS).fill(0);
    let withdrawable = 0;
    let repurchase = 0;
    let playerTime = player[2].toNumber();
    if (playerTime > this.state.gameInfo.doomsday - PERIOD) {
        playerUnits = player[5];     
        withdrawable = player[0].toNumber() / SUN_IN_TRX;
        repurchase = player[1].toNumber() / SUN_IN_TRX;
        let periods = Math.floor((this.time_now - playerTime) / PERIOD);
        if (periods > 0) {
          let profitPerPeriod = 0;
          for (let i = 0; i < NUMBER_OF_UNITS; i++) {
            profitPerPeriod += (this.units[i].profit * playerUnits[i]);
          }
          let profit = periods * profitPerPeriod;
          repurchase += (profit / 2);
          withdrawable += (profit / 2);
        }
    }
    this.setState({playerInfo: {withdrawable : withdrawable, repurchase : repurchase, 
        refEarnings : player[3].toNumber() / SUN_IN_TRX, 
        noFirstUnit : player[4] === ZERO_ADDRESS, time : playerTime, units : playerUnits,
        }});

    this.setState({loading: false});
  }

  async getInfo() {
    if (window.tronWeb) tronWeb = window.tronWeb;
    if (tronWeb && tronWeb.ready) {
      this.contract = await tronWeb.contract().at(CONTRACT_ADDRESS);
      if (tronWeb.isAddress(sponsor) && await this.contract.checkSponsor(sponsor).call()) {
          this.sponsor = sponsor;
          //this.setState({showSponsorWarning: false});
      } //else {this.setState({showSponsorWarning: true});}
      this.account = await tronWeb.trx.getAccount();    
      this.setState({address: tronWeb.address.fromHex(this.account.address)});
      let block = await tronWeb.trx.getCurrentBlock();
      this.time_now = block.block_header.raw_data.timestamp / 1000;
      await this.getGameInfo();
      this.getPlayerInfo();
      this.setState({loading: false});
    }
  }

  async checkSponsor(sponsor_) {
    if (tronWeb.isAddress(sponsor_) && await this.contract.checkSponsor(sponsor_).call()) {
      this.setState({sponsorValid: true, sponsorInvalid: false});
      this.sponsor = sponsor_;
    } else {
      this.setState({sponsorValid: false, sponsorInvalid: true});
    }
  }

  async toggleRefModal() {
    if (this.state.refModal) this.setState({refModal: false});
    else {
      this.setState({refModal: true});
      if (this.contract) {
        let refList = await this.contract.getPlayerRefferalsList().call();
        this.setState({playerRefList: refList.map(item => tronWeb.address.fromHex(item))});
      }
    }
  }

  toggleFAQModal() {
    this.setState({
      faqModal: !this.state.faqModal
    });
  }

  navbarToggle() {
    this.setState({
      navbarOpen: !this.state.navbarOpen
    });
  }

  async deposit(amount) {
    this.setState({loading: true});
    let args = {
      feeLimit:ENERGY_LIMIT,
      callValue:Math.trunc(amount * SUN_IN_TRX),
      shouldPollResponse: false
    };
    if (this.state.playerInfo.time !== 0) await this.contract.deposit().send(args);
    else await this.contract.firstDeposit(this.sponsor).send(args);
  }

  async withdraw(amount) {
    this.setState({loading: true});
    let args = {
      feeLimit: ENERGY_LIMIT,
      callValue: 0,
      shouldPollResponse: false
    };
    await this.contract.withdraw(Math.trunc(amount * SUN_IN_TRX)).send(args);
  }

  async tokens(amount) {
    this.setState({loading: true});
    let args = {
      feeLimit: ENERGY_LIMIT,
      callValue: 0,
      shouldPollResponse: false
    };
    await this.contract.futureContractAction(Math.trunc(amount * SUN_IN_TRX), 0).send(args);
  }

  async buy(type, amount) {
    this.setState({loading: true});
    let args = {
      feeLimit: ENERGY_LIMIT,
      callValue: 0,
      shouldPollResponse: false
    };
    await this.contract.buyUnit(type, Math.trunc(amount)).send(args);
  }

  componentDidMount() {
    this.getInfo()
    this.interval = setInterval(() => this.getInfo(), 4000);
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }
  

  render() {
    if (this.state.gameInfo.doomsday - this.time_now > 0) return (
      <Doomsday time = {this.state.gameInfo.doomsday - this.time_now} />
    );
    let refBox = this.state.playerInfo.time === 0 && this.state.showSponsorWarning;
    let infoLink = (lang == 'ru') ? '/info_ru.html' : '/info.html'
    return (
      <div className="App">
        <Navbar dark fixed="top" expand="lg">
        <div className="container">
          <NavbarBrand href="/"><img src={logo} height="40px" width="40px"/>Tron Worlds</NavbarBrand>
          <NavbarToggler onClick={this.navbarToggle} />
          <Collapse isOpen={this.state.navbarOpen} navbar>
          <Nav className="mr-auto" navbar>
              <NavItem>
                <NavLink href={infoLink} className="text-primary">
                {messages.tutorial}
                </NavLink>
              </NavItem>
            </Nav>
            <h4 className="text-center d-none d-lg-block">{this.state.address}</h4>
            <Nav className="ml-auto" navbar>
              <NavItem>
                <NavLink href="#" className="text-success" onClick = {this.toggleFAQModal}>
                  FAQ
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="#" className="text-info" onClick = {this.toggleRefModal}>
                  {messages.ref_info}
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="https://discord.gg/TW6CAAB">
                  <img src={discord} height="20px" width="20px" />
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="https://t.me/tron_worlds">
                  <img src={telegram} height="20px" width="20px" />
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="/?l=en">
                  <img src={eng} height="20px" width="20px" />
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="/?l=ru">
                  <img src={rus} height="20px" width="20px" />
                </NavLink>
              </NavItem>
            </Nav>
          </Collapse>
          </div>
        </Navbar>
        <div className="content  container">
        <div className="row">
          <div className="col-md-6">
            <GameInfo gameInfo = {this.state.gameInfo}/>
          </div>
          <div className="col-md-6">
            {this.state.playerInfo.time === LOADING ? <DepositBoxLoading /> : <DepositBox playerInfo = {this.state.playerInfo} deposit = {this.deposit} withdraw = {this.withdraw}/>}
          </div>
        </div>
        <Units units = {units} funds = {this.state.playerInfo.withdrawable + this.state.playerInfo.repurchase} playerUnits = {this.state.playerInfo.units} noFirstUnit = {this.state.playerInfo.noFirstUnit} buy = {this.buy} time = {this.state.playerInfo.time}/>
        <GoldBox playerInfo = {this.state.playerInfo} tokens = {this.tokens} address = {this.state.address}/>
        <div className="BottomInfo">
        <p>{messages.game_description}</p>
        </div>
        </div>
        <footer>
        <div className="container">
        <Nav pills justified>
         {footerLinks.map((item, key) => <NavItem key={key}><NavLink href={item.url} target="_blank">{item.name}</NavLink></NavItem>)}
        </Nav>
        </div>
        </footer>
        <ReferralInfo refList={this.state.playerRefList} playerInfo={this.state.playerInfo} address={this.state.address} modal={this.state.refModal} toggleFun={this.toggleRefModal}/>
        <FAQ modal={this.state.faqModal} toggleFun={this.toggleFAQModal}/>
        {refBox && <RefBox checkSponsor={this.checkSponsor} sponsorValid= {this.state.sponsorValid} sponsorInvalid= {this.state.sponsorInvalid}/>}
        {this.state.loading && <div className="spin"><Spinner style={{ width: '4rem', height: '4rem' }} color="success" /></div>}
      </div>
    );
  }
}

function DepositBoxLoading(props) {
    return (
      <Card className="DepositBox">
        <CardBody>
          <CardTitle><h4>{messages.player_info}</h4></CardTitle>
          <CardText dangerouslySetInnerHTML={{__html: startInfo}} />
        </CardBody>
      </Card>
    );
  }

class DepositBox extends Component {

  constructor(props) {
    super(props);
    this.state = { balance : 0 };
    this.withdraw = React.createRef();
    this.deposit = React.createRef();
    this.onWithdraw = this.onWithdraw.bind(this);
    this.onDeposit = this.onDeposit.bind(this);
    this.setAmount = this.setAmount.bind(this);
  }

  async getBalance() {
    if (tronWeb && tronWeb.ready) {
      let balance = await tronWeb.trx.getBalance();
      let new_state = Object.assign({}, this.state);
      new_state.balance = (balance / SUN_IN_TRX).toFixed(2);
      this.setState(new_state);
    }
  }

  componentDidMount() {
    this.interval = setInterval(() => this.getBalance(), 4000);
    this.getBalance();
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  onWithdraw() {
    let amount = Number(this.withdraw.current.value);
    if (amount > 0 && amount <= this.props.playerInfo.withdrawable) this.props.withdraw(amount);
  }

  onDeposit() {
    let amount = Number(this.deposit.current.value);
    if (amount > 0) this.props.deposit(amount);
  }

  setAmount() {
    this.withdraw.current.value = Math.floor(this.props.playerInfo.withdrawable * SUN_IN_TRX) / SUN_IN_TRX;
  }

  render() {
    let player_units = 0;
    let player_hourly_profit = 0;
    for (var i = 0; i < NUMBER_OF_UNITS; i++) {
      player_units += this.props.playerInfo.units[i];
      player_hourly_profit += this.props.playerInfo.units[i] * units[i].profit;
    }
    return (
      <Card className="DepositBox">
        <CardBody>
          <CardTitle><h4>{messages.player_info}</h4></CardTitle>
          <div className="row">
          <CardText className="col-lg-5">
            {messages.your_units}<b>{player_units}</b><br />
            <span id="repurchase">{messages.repurchase}<b>{this.props.playerInfo.repurchase.toFixed(2)}</b> TRX</span>
          </CardText>
          <CardText className="col-lg-7">
          {messages.hourly_profit}<b>{player_hourly_profit.toFixed(2)}</b> TRX<br />
            <span id="withdrawable">{messages.withdrawable}<b>{this.props.playerInfo.withdrawable.toFixed(2)}</b> TRX</span>
          </CardText>
          </div>
          <UncontrolledTooltip placement="top" target="withdrawable">
            {messages.with_tip}
          </UncontrolledTooltip>
          <UncontrolledTooltip placement="top" target="repurchase">
            {messages.rep_tip}
          </UncontrolledTooltip>
          <UncontrolledTooltip placement="top" target="deposit">
            {messages.dep_tip}
          </UncontrolledTooltip>
          <div className="row">
          <InputGroup className="col-lg-5" id="deposit">
            <Input type="text" innerRef={this.deposit} defaultValue = "100"/>
            <InputGroupAddon addonType="append">
              <Button color="danger" onClick = {this.onDeposit}>
              {messages.deposit}
              </Button>
            </InputGroupAddon>
          </InputGroup>
          <InputGroup className="col-lg-7">
            <Input type="text" innerRef={this.withdraw} defaultValue = "1"/>
            <InputGroupAddon addonType="append">
              <Button color="secondary" onClick={this.setAmount}>
              {messages.max}
              </Button>
              <Button color="success" onClick = {this.onWithdraw}>
              {messages.withdraw}
              </Button>
            </InputGroupAddon>
          </InputGroup>
          </div>
          <CardText className="text-left"><small className="text-muted">{messages.wallet}<b>{this.state.balance}</b> TRX</small></CardText>
        </CardBody>
      </Card>
    );
  }
}

function GameInfo(props) {
  let startDate = new Date(props.gameInfo.doomsday * 1000);
  return (
      <Card className="GameInfo">   
        <CardBody>
          <CardTitle><h4>{messages.game_info}</h4></CardTitle>
          <CardText>
            {messages.buys}<b>{(props.gameInfo.balance + props.gameInfo.payouts).toLocaleString('en', {maximumFractionDigits: 2})} TRX</b><br />
            {messages.payouts}<b>{props.gameInfo.payouts.toLocaleString('en', {maximumFractionDigits: 2})} TRX</b><br />
            {messages.players}<b>{props.gameInfo.players}</b><br />
            {messages.active_units}<b>{props.gameInfo.units}</b><br />
            {messages.started}<b>{startDate.toLocaleDateString()}</b>
          </CardText>
        </CardBody>
      </Card>
  );
}

