class Unit extends Component  {

  constructor(props) {
    super(props);
    this.buy = React.createRef();
    this.onBuy = this.onBuy.bind(this);
  }

  onBuy() {
    let amount = Number(this.buy.current.value);
    if (amount * this.props.price > this.props.funds) amount = Math.floor(this.props.funds / this.props.price)
    if (amount > 0) this.props.buy(this.props.id, amount);
  }

  render() {
    let disabled = this.props.disabled
    let buyButton = "buy-button" + this.props.id;
    return(
      <div className="col-md-6 col-xl-4">
      <Card className="Unit">
        <CardBody>
          <CardImg top width="100%" src={this.props.pic} alt={this.props.name}/>
          <CardTitle><b>{this.props.name}</b></CardTitle>
          <CardText className="text-warning">{this.props.description}</CardText>
          <CardText>
            {messages.per_hour}<b>{this.props.profit}</b> TRX<br />
            {messages.per_month}<b>{((this.props.profit * 72000) / this.props.price).toFixed()}</b> %<br />
            {messages.price}<b>{this.props.price}</b> TRX<br />
            {messages.you_own}<b>{this.props.quantity}</b>
          </CardText>
          {!disabled && ((this.props.price > this.props.funds) ? <CardText className="text-danger">{messages.more_funds}</CardText> : <InputGroup>
          <InputGroupAddon addonType="prepend">
            <InputGroupText>#</InputGroupText>
          </InputGroupAddon>
          <Input type="text" innerRef={this.buy} defaultValue = "1"/>
          <InputGroupAddon addonType="append">
            <Button id={buyButton} color="warning" onClick = {this.onBuy}>
              {messages.buy}
            </Button>
          </InputGroupAddon>
          <UncontrolledTooltip placement="top" target={buyButton}>
            {messages.buy_description}
          </UncontrolledTooltip>
          </InputGroup>)}
        </CardBody>
      </Card>
      </div>
    );
  }
}

class RefBox extends Component  {

  constructor(props) {
    super(props);
    this.input = React.createRef();
    this.onCheck = this.onCheck.bind(this);
    this.closeFun = this.closeFun.bind(this);
    this.state = { modal:true };
  }

  onCheck() {
    this.props.checkSponsor(this.input.current.value);
  }

  closeFun() {
    this.setState({ modal:false });
  }

  render() {
    return(
      <Modal isOpen={this.state.modal}>
    <ModalHeader>Your Sponsor</ModalHeader>
    <ModalBody>
      <p dangerouslySetInnerHTML={{__html: sponsorWarning}} />
      <InputGroup>
        <Input valid={this.props.sponsorValid} invalid={this.props.sponsorInvalid} type="text" innerRef={this.input} defaultValue = "" />
        <InputGroupAddon addonType="append">
          <Button color="primary" onClick = {this.onCheck}>
            Check
          </Button>
        </InputGroupAddon>
       </InputGroup>
       </ModalBody>
    <ModalFooter>
      {(this.props.sponsorValid) ? <Button color="success" onClick={this.closeFun}>Ok</Button> : <Button color="primary" onClick={this.closeFun}>I have no sponsor</Button>}
    </ModalFooter>
    </Modal>
    );
  }
}

class Doomsday extends Component  {

  constructor(props) {
    super(props);
    this.state = {
      time: this.props.time,
      hours: "00",
      minutes: "00",
      seconds: "00",
    };
  }

  tick() {
    let time = this.state.time
    if (this.state.time < 0) window.location.reload();
    let hours = Math.floor(time / 3600);
    let minutes = Math.floor((time - hours*3600) / 60);
    let seconds = Math.floor(time - (hours*3600 + minutes*60));
    if (hours < 10) hours = "0" + hours;
    if (minutes < 10) minutes = "0" + minutes;
    if (seconds < 10) seconds = "0" + seconds;
    this.setState({time: time - 1, hours: hours, minutes: minutes, seconds: seconds});
  }


  componentDidMount() {
    this.interval = setInterval(() => this.tick(), 1000);
    this.tick()
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  render() {
    return(
      <div className="Doomsday text-center">
      <h1>{messages.rebirth}</h1>
      <h2>{messages.counter}</h2>
      <h1>{this.state.hours}:{this.state.minutes}:{this.state.seconds}</h1>
      </div>
    );
  }
}

class GoldBox extends Component {

  constructor(props) {
    super(props);
    this.state = { balance : 0 };
    this.tokens = React.createRef();
    this.onTokens = this.onTokens.bind(this);
    this.setAmount = this.setAmount.bind(this);
    this.getBalance = this.getBalance.bind(this);
  }

  async getBalance() {
    if (tronWeb && tronWeb.ready) {
      this.contract = await window.tronWeb.contract().at(TWGOLD_CONTRACT_ADDRESS);
      let balance = await this.contract.balanceOf(this.props.address).call();
      let new_state = Object.assign({}, this.state);
      new_state.balance = (balance / SUN_IN_TRX).toFixed(2);
    this.setState(new_state);
    }
  }

  async componentDidMount() {
    this.interval = setInterval(() => this.getBalance(), 4000);
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  onTokens() {
    let amount = Number(this.tokens.current.value);
    if (amount > 0 && amount <= this.props.playerInfo.withdrawable + this.props.playerInfo.repurchase) this.props.tokens(amount);
  }

  setAmount() {
    this.tokens.current.value = Math.floor(this.props.playerInfo.repurchase * SUN_IN_TRX) / SUN_IN_TRX;
  }

  render() {
    return (
      <Card className="GoldBox">
        <CardBody>
          <CardTitle><h4>{messages.tokens}</h4></CardTitle>
          <CardText dangerouslySetInnerHTML={{__html:twGoldInfo}} />
          <InputGroup>
            <InputGroupAddon addonType="prepend">TWGold</InputGroupAddon>
            <Input type="text" innerRef={this.tokens} defaultValue = "1"/>
            <InputGroupAddon addonType="append">
              <Button color="secondary" onClick={this.setAmount}>
                {messages.max_rep}
              </Button>
              <Button color="success" disabled={true} onClick = {this.onTokens}>
                {messages.buy_tokens}
              </Button>
            </InputGroupAddon>
          </InputGroup>
          <CardText className="text-left"><small className="text-muted">{messages.token_balance}<b>{this.state.balance}</b> TWGold</small></CardText>
        </CardBody>
      </Card>
    );
  }
}

export default App;
